export default {
  state: {
    services: [
      {id: '1', name: 'Название услуги', price: '0', description: 'Lorem ipsum, dolor sit amet consectetur adipisicing elit. Necessitatibus, repellendus.'}
    ]
  },
  mutations: {
    ADD_SERVICES(state, payload) {
      state.services.push(payload)
    },
    DELETE_SERVICES(state, payload) {
      state.services.splice(payload, 1)
    },
    EDIT_SERVICES(state, {idx, name, price, description}) {
      state.services[idx].name = name
      state.services[idx].price = price
      state.services[idx].description = description
    }
  },
  actions: {
    addServices({commit}, payload) {
      console.log(payload)
      commit('ADD_SERVICES', payload)
    },
    deleteServices({commit}, payload) {
      commit('DELETE_SERVICES', payload)
    },
    editServices({commit}, payload) {
      commit('EDIT_SERVICES', payload)
    }
  },
  getters: {
    getServicesInfo(state) {
      return state.services
    }
  } 
}