import Vue from 'vue'
import 'vuetify/dist/vuetify.min.css'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import router from './plugins/router'
import store from './store'

Vue.config.productionTip = true

new Vue({
  vuetify,
  router,
  store,
  render: h => h(App)
}).$mount('#app')
