export default {
  state: {
    clients: {
      list: [
        {name: 'Василий Васильевич', email: 'sample@mail.com', phone: '+79615681285'}
      ]
    }
  },
  mutations: {
    ADD_CLIENT(state, payload) {
      state.clients.list.push(payload)
    }
  },
  actions: {
    addClient({commit}, payload) {
      commit('ADD_CLIENT', payload)
    }
  },
  getters: {
    getClientsList(state) {
      return state.clients
    }
  }
}