import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/', 
    name: 'Home', 
    component: () => import ('@/components/Home')
  },
  {
    path: '/clients',
    name: 'Clients',
    component: () => import ('@/components/Clients')
  },
  {
    path: '/services',
    name: 'Services',
    component: () => import ('@/components/Services')
  },
  {
    path: '/finances',
    name: 'Finances',
    component: () => import ('@/components/Finances')
  },
  {
    path: '/404',
    name: 'NotFoundPage',
    component: () => import ('@/components/NotFoundPage')
  },
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
